import React from 'react';
import {
  TextField,
  Typography,
  Button,
  Box,
  Grid,
  Paper
} from '@mui/material';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';

const validationSchema = Yup.object().shape({
  firstName: Yup.string()
    .min(2, '     2 ')
    .required(' '),
  lastName: Yup.string()
    .min(2, '     2 ')
    .required(' '),
  idNumber: Yup.string()
    .matches(/^\d{9}$/, '     9 ')
    .test('valid-israeli-id', '    ', (value) => {
      if (!value) return false;
      // Israeli ID validation algorithm
      const id = String(value).trim();
      if (id.length !== 9 || isNaN(id)) return false;
      
      const sum = id
        .split('')
        .map((num, idx) => {
          const digit = Number(num);
          let calc = digit * ((idx % 2) + 1);
          calc = calc > 9 ? calc - 9 : calc;
          return calc;
        })
        .reduce((acc, curr) => acc + curr, 0);
      
      return sum % 10 === 0;
    })
    .required(' '),
  birthDate: Yup.date()
    .max(new Date(), '    ')
    .test('minimum-age', '   18', function(value) {
      if (!value) return false;
      const cutoff = new Date();
      cutoff.setFullYear(cutoff.getFullYear() - 18);
      return value <= cutoff;
    })
    .required(' '),
  address: Yup.string()
    .min(5, '    5 ')
    .required(' '),
  phone: Yup.string()
    .matches(/^05\d{8}$/, '   ')
    .required(' '),
  email: Yup.string()
    .email('   ')
    .required(' ')
});

const PersonalDetailsStep = ({ onNext, onSubmit, data = {} }) => {
  const initialValues = {
    firstName: data?.personalDetails?.firstName || '',
    lastName: data?.personalDetails?.lastName || '',
    idNumber: data?.personalDetails?.idNumber || '',
    birthDate: data?.personalDetails?.birthDate || '',
    address: data?.personalDetails?.address || '',
    phone: data?.personalDetails?.phone || '',
    email: data?.personalDetails?.email || ''
  };

  const handleFormSubmit = (values) => {
    onNext({
      target: {
        name: 'personalDetails',
        value: {
          firstName: values.firstName || '',
          lastName: values.lastName || '',
          idNumber: values.idNumber || '',
          birthDate: values.birthDate || '',
          address: values.address || '',
          phone: values.phone || '',
          email: values.email || ''
        }
      }
    });
    onSubmit();
  };

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={handleFormSubmit}
    >
      {({ values, setFieldValue, errors, touched }) => (
        <Form>
          <Typography 
            variant="h5" 
            gutterBottom 
            align="center"
            sx={{
              fontSize: { xs: '1.2rem', sm: '1.5rem' },
              mb: { xs: 2, sm: 3 }
            }}
          >
                    
          </Typography>
          
          <Grid container spacing={{ xs: 2, sm: 3 }} sx={{ mb: { xs: 2, sm: 3 } }}>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                name="firstName"
                label=" "
                value={values.firstName}
                onChange={(e) => setFieldValue('firstName', e.target.value)}
                error={touched.firstName && Boolean(errors.firstName)}
                helperText={touched.firstName && errors.firstName}
                sx={{
                  '& .MuiInputBase-root': {
                    height: { xs: '45px', sm: '56px' }
                  }
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                name="lastName"
                label=" "
                value={values.lastName}
                onChange={(e) => setFieldValue('lastName', e.target.value)}
                error={touched.lastName && Boolean(errors.lastName)}
                helperText={touched.lastName && errors.lastName}
                sx={{
                  '& .MuiInputBase-root': {
                    height: { xs: '45px', sm: '56px' }
                  }
                }}
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                name="idNumber"
                label=" "
                value={values.idNumber}
                onChange={(e) => setFieldValue('idNumber', e.target.value)}
                error={touched.idNumber && Boolean(errors.idNumber)}
                helperText={touched.idNumber && errors.idNumber}
                sx={{
                  '& .MuiInputBase-root': {
                    height: { xs: '45px', sm: '56px' }
                  }
                }}
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                type="date"
                name="birthDate"
                label=" "
                value={values.birthDate}
                onChange={(e) => setFieldValue('birthDate', e.target.value)}
                error={touched.birthDate && Boolean(errors.birthDate)}
                helperText={touched.birthDate && errors.birthDate}
                InputLabelProps={{ shrink: true }}
                sx={{
                  '& .MuiInputBase-root': {
                    height: { xs: '45px', sm: '56px' }
                  }
                }}
              />
            </Grid>
            
            <Grid item xs={12}>
              <TextField
                fullWidth
                name="address"
                label=""
                value={values.address}
                onChange={(e) => setFieldValue('address', e.target.value)}
                error={touched.address && Boolean(errors.address)}
                helperText={touched.address && errors.address}
                sx={{
                  '& .MuiInputBase-root': {
                    height: { xs: '45px', sm: '56px' }
                  }
                }}
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                name="phone"
                label=" "
                value={values.phone}
                onChange={(e) => setFieldValue('phone', e.target.value)}
                error={touched.phone && Boolean(errors.phone)}
                helperText={touched.phone && errors.phone}
                sx={{
                  '& .MuiInputBase-root': {
                    height: { xs: '45px', sm: '56px' }
                  }
                }}
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                name="email"
                label=""
                value={values.email}
                onChange={(e) => setFieldValue('email', e.target.value)}
                error={touched.email && Boolean(errors.email)}
                helperText={touched.email && errors.email}
                sx={{
                  '& .MuiInputBase-root': {
                    height: { xs: '45px', sm: '56px' }
                  }
                }}
              />
            </Grid>
          </Grid>

          <Box sx={{ 
            display: 'flex', 
            justifyContent: 'flex-end',
            mt: { xs: 2, sm: 3 },
            '& .MuiButton-root': {
              width: { xs: '100%', sm: 'auto' },
              minHeight: { xs: '44px', sm: 'auto' }
            }
          }}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              size="large"
            >
              
            </Button>
          </Box>
        </Form>
      )}
    </Formik>
  );
};

export default PersonalDetailsStep;
