import React, { useState, useEffect } from 'react';
import {
  Stepper,
  Step,
  StepLabel,
  Button,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  IconButton,
  Tooltip,
  useMediaQuery,
  CircularProgress,
  Box
} from '@mui/material';
import { makeStyles } from '@mui/styles';
import { motion, AnimatePresence } from 'framer-motion';
import InfoIcon from '@mui/icons-material/Info';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import rtlPlugin from 'stylis-plugin-rtl';
import { CacheProvider } from '@emotion/react';
import createCache from '@emotion/cache';
import { prefixer } from 'stylis';
import emailjs from '@emailjs/browser';
import MaritalStatusStep from './steps/MaritalStatusStep';
import EmploymentStatusStep from './steps/EmploymentStatusStep';
import IncomeStep from './steps/IncomeStep';
import JobHistoryStep from './steps/JobHistoryStep';
import AdditionalCriteriaStep from './steps/AdditionalCriteriaStep';
import PersonalDetailsStep from './steps/PersonalDetailsStep';

// Initialize EmailJS with your public key
emailjs.init("7ggM2WNflMbliCnaP");

const useStyles = makeStyles((theme) => ({
  formContainer: {
    maxWidth: '800px',
    margin: '20px auto',
    padding: '30px',
    backgroundColor: '#ffffff',
    borderRadius: '12px',
    boxShadow: '0 8px 32px rgba(0, 0, 0, 0.1)',
    position: 'relative',
    '@media (max-width: 600px)': {
      margin: '10px',
      padding: '15px',
      borderRadius: '8px',
      maxWidth: '100%',
    },
  },
  titleContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    gap: '10px',
    marginBottom: '30px',
    marginTop: '-5px',
    flexWrap: 'wrap',
    '@media (max-width: 600px)': {
      marginBottom: '20px',
      gap: '5px',
    },
  },
  title: {
    textAlign: 'center',
    color: '#1a237e',
    fontSize: '24px',
    fontWeight: 500,
    paddingBottom: '20px',
    '@media (max-width: 600px)': {
      fontSize: '20px',
      paddingBottom: '15px',
    },
  },
  infoIcon: {
    color: '#1a237e',
    cursor: 'pointer',
    fontSize: '20px',
    '&:hover': {
      color: '#303f9f',
    },
  },
  stepProgress: {
    textAlign: 'center',
    color: '#666',
    fontSize: '16px',
    marginTop: '-15px',
    marginBottom: '20px',
    '@media (max-width: 600px)': {
      fontSize: '14px',
      marginTop: '-10px',
      marginBottom: '15px',
    },
  },
  stepper: {
    backgroundColor: 'transparent',
    padding: '20px 0',
    '@media (max-width: 600px)': {
      padding: '10px 0',
      '& .MuiStepConnector-root': {
        display: 'none',
      },
      '& .MuiStep-root': {
        padding: '0 4px',
      },
    },
  },
  stepLabel: {
    '& .MuiStepLabel-label': {
      fontSize: '16px',
      '@media (max-width: 600px)': {
        fontSize: '12px',
      },
    },
    '& .MuiStepLabel-completed': {
      color: '#4caf50',
    },
  },
  stepIcon: {
    color: '#1a237e',
  },
  completed: {
    color: '#1a237e !important',
  },
  genderNote: {
    textAlign: 'center',
    color: '#666',
    fontSize: '14px',
    marginTop: '10px',
    marginBottom: '20px',
    fontStyle: 'italic'
  },
  loadingOverlay: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'rgba(255, 255, 255, 0.8)',
    zIndex: 1000,
    borderRadius: '12px',
  },
  buttonContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    marginTop: '30px',
    gap: '16px',
    '@media (max-width: 600px)': {
      flexDirection: 'column-reverse',
      gap: '10px',
    },
  },
  button: {
    minWidth: '120px',
    '@media (max-width: 600px)': {
      width: '100%',
      minHeight: '44px',
    },
  },
}));

const TaxRefundForm = () => {
  const classes = useStyles();
  const isMobile = useMediaQuery('(max-width:600px)');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [showInfoDialog, setShowInfoDialog] = useState(false);
  const [showUnemployedDialog, setShowUnemployedDialog] = useState(false);
  const [showSelfEmployedDialog, setShowSelfEmployedDialog] = useState(false);
  const [showLowIncomeDialog, setShowLowIncomeDialog] = useState(false);
  const [showSuccessDialog, setShowSuccessDialog] = useState(false);

  const initialFormData = {
    maritalStatus: '',
    employmentStatus: '',
    income: '',
    severancePay: '',
    jobHistory: '',
    additionalCriteria: [],
    personalDetails: {}
  };

  const [activeStep, setActiveStep] = useState(0);
  const [formData, setFormData] = useState(initialFormData);
  const [errors, setErrors] = useState({});
  const [score, setScore] = useState(0);
  const [leadQuality, setLeadQuality] = useState('');

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData(prevData => {
      if (name === 'personalDetails') {
        // Handle the entire personalDetails object update
        return {
          ...prevData,
          personalDetails: {
            ...prevData.personalDetails,
            ...value
          }
        };
      } else if (name.startsWith('personalDetails.')) {
        // Handle individual personalDetails field updates
        const field = name.split('.')[1];
        return {
          ...prevData,
          personalDetails: {
            ...prevData.personalDetails,
            [field]: value
          }
        };
      }
      return {
        ...prevData,
        [name]: value
      };
    });
    // Clear error when user makes a change
    setErrors(prev => ({
      ...prev,
      [name]: ''
    }));
  };

  // Function to adjust iframe height
  useEffect(() => {
    const updateIframeHeight = () => {
      const height = document.documentElement.scrollHeight;
      window.parent.postMessage({ type: 'setHeight', height }, '*');
    };

    updateIframeHeight();
    window.addEventListener('resize', updateIframeHeight);
    return () => window.removeEventListener('resize', updateIframeHeight);
  }, [activeStep]);

  const calculateScore = (newData) => {
    let newScore = 0;
    let scoreDetails = [];
    
    //  
    if (newData.maritalStatus === 'married') {
      newScore += 3;
      scoreDetails.push('/: 3 ');
    } else if (newData.maritalStatus) {
      newScore += 2;
      scoreDetails.push('/ / / / /: 2 ');
    }
    
    //  
    if (newData.employmentStatus === 'employed') {
      newScore += 5;
      scoreDetails.push(': 5 ');
    } else if (newData.employmentStatus === 'bothEmployedAndSelfEmployed') {
      newScore += 3;
      scoreDetails.push(' + : 3 ');
    }
    
    //  
    if (newData.income === 'above7000') {
      newScore += 5;
      scoreDetails.push('  7,000 ": 5 ');
    }
    
    //  
    if (newData.jobHistory === 'changed') {
      newScore += 4;
      scoreDetails.push(' : 4 ');
    } else if (newData.jobHistory === 'same') {
      newScore += 2;
      scoreDetails.push('  : 2 ');
    }
    
    //  
    const additionalCriteriaPoints = {
      unemployment: ['  ', 3],
      propertyTax: [' ', 3],
      securities: ['  ', 3],
      lifeInsurance: [' ', 3],
      pensionDeposit: ['  ', 3],
      donations: ['', 3],
      disability: ['', 3],
      militaryService: [' "', 3],
      education: [' ', 3],
      rentalIncome: ['  ', 3],
      newImmigrant: [' ', 3]
    };
    
    newData.additionalCriteria?.forEach(criteria => {
      if (additionalCriteriaPoints[criteria]) {
        newScore += additionalCriteriaPoints[criteria][1];
        scoreDetails.push(`${additionalCriteriaPoints[criteria][0]}: ${additionalCriteriaPoints[criteria][1]} `);
      }
    });

    setScore(newScore);
    
    //   
    let quality = '';
    if (newScore >= 30) {
      quality = '  ';
    } else if (newScore >= 20) {
      quality = ' ';
    } else if (newScore >= 15) {
      quality = ' ';
    } else {
      quality = ' ';
    }
    
    setLeadQuality(quality);
    return { score: newScore, quality, details: scoreDetails };
  };

  const validateStep = () => {
    const newErrors = {};
    
    switch (activeStep) {
      case 0:
        if (!formData.maritalStatus) {
          newErrors.maritalStatus = '   ';
        }
        break;
      // Add validation for other steps here
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const isStepValid = () => {
    switch (activeStep) {
      case 0:
        return Boolean(formData.maritalStatus);
      case 1:
        return Boolean(formData.employmentStatus);
      case 2:
        return Boolean(formData.income) && Boolean(formData.severancePay);
      case 3:
        return Boolean(formData.jobHistory);
      case 4:
        return true; // No validation required for additional criteria
      case 5:
        const { firstName, lastName, phone, email } = formData.personalDetails || {};
        return Boolean(firstName && lastName && phone && email);
      default:
        return false;
    }
  };

  const handleNext = () => {
    if (!isStepValid()) {
      let errorMessage = '';
      switch (activeStep) {
        case 0:
          errorMessage = '   ';
          break;
        case 1:
          errorMessage = '   ';
          break;
        case 2:
          if (!formData.income) {
            errorMessage = '   ';
          } else if (!formData.severancePay) {
            errorMessage = '     ';
          }
          break;
        case 3:
          errorMessage = '     ';
          break;
        case 4:
          errorMessage = '    ';
          break;
        case 5:
          errorMessage = '     ';
          break;
      }
      
      setErrors(prev => ({
        ...prev,
        [Object.keys(formData)[activeStep]]: errorMessage
      }));
      return;
    }

    // Check employment status logic
    if (activeStep === 1) {
      if (formData.employmentStatus === 'unemployed') {
        setShowUnemployedDialog(true);
        return;
      } else if (formData.employmentStatus === 'selfEmployed') {
        setShowSelfEmployedDialog(true);
        return;
      }
    }

    // Check income and severance pay logic
    if (activeStep === 2) {
      if (formData.income === 'below7000' && formData.severancePay === 'no') {
        setShowLowIncomeDialog(true);
        return;
      }
    }

    if (activeStep === steps.length - 1) {
      handleSubmit();
    } else {
      setActiveStep((prevStep) => prevStep + 1);
    }
  };

  const handleBack = () => {
    setActiveStep((prevStep) => prevStep - 1);
  };

  const handleSubmit = async () => {
    setIsSubmitting(true);
    try {
      console.log('Form data before submit:', formData); // Debug log
      const scoreResult = calculateScore(formData);
      
      // Create template params for email
      const templateParams = {
        to_name: '',
        from_name: formData.personalDetails?.firstName 
          ? `${formData.personalDetails.firstName} ${formData.personalDetails.lastName}`
          : ' ',
        to_email: 'omerh@yuvalim-ins.co.il',
        subject: `     - ${scoreResult.quality}`,
        message: `
 : ${formData.personalDetails?.firstName || ''} ${formData.personalDetails?.lastName || ''}
: ${formData.personalDetails?.phone || ''}
: ${formData.personalDetails?.email || ''}
 : ${formData.personalDetails?.idNumber || ''}
 : ${formData.personalDetails?.birthDate || ''}
: ${formData.personalDetails?.address || ''}

 : ${formData.maritalStatus === 'married' ? '/' : '/ / / / /'}
 : ${formData.employmentStatus === 'employed' ? '' : 
                formData.employmentStatus === 'selfEmployed' ? '' : 
                formData.employmentStatus === 'bothEmployedAndSelfEmployed' ? ' + ' : ' '}
 : ${formData.income === 'above7000' ? ' 7,000 "' : ' -7,000 "'}
 : ${formData.severancePay === 'yes' ? '' : ''}
 : ${formData.jobHistory === 'changed' ? '' : ''}

 :
${formData.additionalCriteria?.map(criteria => {
  const labels = {
    unemployment: '  ',
    propertyTax: ' ',
    securities: '  ',
    lifeInsurance: ' ',
    pensionDeposit: '  ',
    donations: '',
    disability: '',
    militaryService: ' "',
    education: ' ',
    rentalIncome: '  ',
    newImmigrant: ' '
  };
  return '- ' + labels[criteria];
}).join('\n') || ''}

: ${scoreResult.score}
 : ${scoreResult.quality}

 :
${scoreResult.details.join('\n')}

 : ${new Date().toLocaleString('he-IL', { timeZone: 'Asia/Jerusalem' })}
`,
        firstName: formData.personalDetails?.firstName || '',
        lastName: formData.personalDetails?.lastName || '',
        phone: formData.personalDetails?.phone || '',
        email: formData.personalDetails?.email || '',
        idNumber: formData.personalDetails?.idNumber || '',
        birthDate: formData.personalDetails?.birthDate || '',
        address: formData.personalDetails?.address || '',
        maritalStatus: formData.maritalStatus === 'married' ? '/' : '/ / / / /',
        employmentStatus: formData.employmentStatus === 'employed' ? '' : 
                         formData.employmentStatus === 'selfEmployed' ? '' : 
                         formData.employmentStatus === 'bothEmployedAndSelfEmployed' ? ' + ' : ' ',
        income: formData.income === 'above7000' ? ' 7,000 "' : ' -7,000 "',
        severancePay: formData.severancePay === 'yes' ? '' : '',
        jobHistory: formData.jobHistory === 'changed' ? '' : '',
        additionalCriteria: formData.additionalCriteria?.map(criteria => {
          const labels = {
            unemployment: '  ',
            propertyTax: ' ',
            securities: '  ',
            lifeInsurance: ' ',
            pensionDeposit: '  ',
            donations: '',
            disability: '',
            militaryService: ' "',
            education: ' ',
            rentalIncome: '  ',
            newImmigrant: ' '
          };
          return labels[criteria];
        }).join('\n') || '',
        score: scoreResult.score,
        quality: scoreResult.quality,
        scoreDetails: scoreResult.details.join('\n'),
        submissionDate: new Date().toLocaleString('he-IL', { timeZone: 'Asia/Jerusalem' })
      };

      console.log('Template params:', templateParams); // Debug log

      // Send email
      const response = await emailjs.send(
        'service_mg36429',
        'template_isk33ym',
        templateParams
      );

      if (response.status === 200) {
        setShowSuccessDialog(true);
      } else {
        console.error('Failed to send email:', response);
        alert('   .     .');
      }
    } catch (error) {
      console.error('Error submitting form:', error);
      alert('   .     .');
    } finally {
      setIsSubmitting(false);
    }
  };

  const resetForm = () => {
    setFormData(initialFormData);
    setActiveStep(0);
    setScore(0);
    setLeadQuality('');
    setShowSuccessDialog(false);
  };

  const steps = [' ', ' ', ' ', '  ', ' ', ' '];

  const getStepContent = (step) => {
    switch (step) {
      case 0:
        return (
          <MaritalStatusStep
            formData={formData}
            handleChange={handleInputChange}
            error={errors.maritalStatus}
          />
        );
      case 1:
        return <EmploymentStatusStep formData={formData} handleChange={handleInputChange} error={errors.employmentStatus} />;
      case 2:
        return <IncomeStep formData={formData} handleChange={handleInputChange} error={errors.income} />;
      case 3:
        return <JobHistoryStep formData={formData} handleChange={handleInputChange} error={errors.jobHistory} />;
      case 4:
        return <AdditionalCriteriaStep formData={formData} handleChange={handleInputChange} error={errors.additionalCriteria} />;
      case 5:
        return <PersonalDetailsStep 
          data={formData} 
          onNext={handleInputChange} 
          onSubmit={handleSubmit}
          error={errors.personalDetails} 
        />;
      default:
        return 'Unknown step';
    }
  };

  const isLastStep = activeStep === steps.length - 1;

  return (
    <div className={classes.formContainer}>
      {isSubmitting && (
        <div className={classes.loadingOverlay}>
          <CircularProgress size={60} thickness={4} sx={{ color: '#1a237e' }} />
        </div>
      )}
      <Dialog
        open={showSuccessDialog}
        onClose={resetForm}
        aria-labelledby="success-dialog-title"
      >
        <DialogTitle id="success-dialog-title">
            !
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
               .      .
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={resetForm}></Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={showUnemployedDialog}
        onClose={() => setShowUnemployedDialog(false)}
        aria-labelledby="unemployed-dialog-title"
        aria-describedby="unemployed-dialog-description"
      >
        <DialogTitle id="unemployed-dialog-title">
          {"   "}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="unemployed-dialog-description">
                  2019 - 2024          
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowUnemployedDialog(false)} color="primary" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={showSelfEmployedDialog}
        onClose={() => setShowSelfEmployedDialog(false)}
        aria-labelledby="self-employed-dialog-title"
      >
        <DialogTitle id="self-employed-dialog-title">
          {"   "}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
                  2019 - 2024      .             .            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowSelfEmployedDialog(false)} color="primary" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={showLowIncomeDialog}
        onClose={() => setShowLowIncomeDialog(false)}
        aria-labelledby="low-income-dialog-title"
      >
        <DialogTitle id="low-income-dialog-title">
          {""}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
                   
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowLowIncomeDialog(false)} color="primary" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={showInfoDialog}
        onClose={() => setShowInfoDialog(false)}
        aria-labelledby="info-dialog-title"
      >
        <DialogTitle id="info-dialog-title">
             
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <Typography paragraph>
                            .
            </Typography>
            <Typography paragraph>
                     , :
            </Typography>
            <ul>
              <li> </li>
              <li>  </li>
              <li>   </li>
              <li>  </li>
              <li>...</li>
            </ul>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowInfoDialog(false)}></Button>
        </DialogActions>
      </Dialog>

      <div className={classes.progressContainer}>
        <Stepper activeStep={activeStep} alternativeLabel className={classes.stepper}>
          {steps.map((label, index) => (
            <Step key={label}>
              <StepLabel 
                className={classes.stepLabel}
                StepIconProps={{
                  classes: {
                    root: classes.stepIcon,
                    completed: classes.completed,
                  },
                }}
              >
                {label}
              </StepLabel>
            </Step>
          ))}
        </Stepper>
        <Typography className={classes.stepProgress}>
           {activeStep + 1}  {steps.length}
        </Typography>
      </div>
      
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: -20 }}
        transition={{ duration: 0.3 }}
        className={classes.formContent}
      >
        <div className={classes.titleContainer}>
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '10px' }}>
            <Typography 
              variant="h6" 
              component="h1" 
              align="center"
              sx={{ 
                fontSize: '1.1rem',
                fontWeight: 500,
                marginBottom: 0,
                paddingBottom: 0
              }}
            >
                 
              <Tooltip title="   -2 ">
                <InfoIcon 
                  sx={{ 
                    fontSize: '1rem',
                    marginRight: '8px',
                    color: 'rgba(0, 0, 0, 0.54)',
                    verticalAlign: 'middle'
                  }} 
                />
              </Tooltip>
            </Typography>

            <Typography 
              sx={{ 
                textAlign: 'center',
                color: '#666',
                fontSize: '14px',
                margin: 0,
                fontStyle: 'italic'
              }}
            >
              *        
            </Typography>
          </div>
        </div>

        {getStepContent(activeStep)}
        
        <Box sx={{ mt: 4, display: 'flex', justifyContent: 'center', gap: 2 }}>
          {activeStep > 0 && (
            <Button
              onClick={handleBack}
              variant="outlined"
            >
              
            </Button>
          )}
          {!isLastStep && (
            <Button
              variant="contained"
              onClick={handleNext}
              disabled={!isStepValid()}
            >
              
            </Button>
          )}
        </Box>
      </motion.div>
    </div>
  );
};

export default TaxRefundForm;
